.transactionHistory {
  margin: 0 auto;
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #cdc1c1;
  padding-top: 100px;
}
.tablebord {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  border: 1px solid rgb(12, 12, 12);
  width: 300px;
  margin-bottom: 50px;
}
.head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: aqua;
  width: 300px;
}
.headName {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid rgb(12, 12, 12);
  width: 100%;
}
.bord {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.tablefull {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgb(220, 228, 228);
  width: 300px;
}
.tablefull2 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgb(103, 107, 107);
  width: 300px;
}
